@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700&display=swap");
html {
  box-sizing: border-box;
  font-size: 62.5%;
  z-index: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 1.6rem;
}

@keyframes grow {
  0% {
    transform: scale(1);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.welcome-section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(to right top, #f6d365, #dd825b);
  color: #000;
}

h1 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 5.5rem;
  text-align: center;
  font-weight: 700;
  line-height: 1.5;
  color: #222;
}
@media (min-width: 768px) {
  h1 {
    font-size: 7rem;
  }
}
h1 svg {
  display: inline-block;
  width: 6rem;
  transform: translateY(0.7rem);
}
h1 span {
  display: block;
  width: fit-content;
  margin: 0 auto;
  text-align: center;
  font-size: 4.5rem;
  color: #dc742f;
  border-bottom: 4px double rgba(68, 68, 68, 0.6823529412);
}
@media (min-width: 768px) {
  h1 span {
    font-size: 5rem;
  }
}

.navbar {
  background-image: linear-gradient(to right, #e59618, #dc742f);
  border-bottom: 1px solid rgba(68, 68, 68, 0.1411764706);
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
}

ul {
  display: flex;
  justify-content: center;
  list-style: none;
}
@media (min-width: 768px) {
  ul {
    justify-content: flex-start;
  }
}
ul li {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  padding: 2rem 0;
}
ul li:hover {
  background-color: #dc742f;
  animation: grow 100ms ease-in;
}
ul li a {
  padding: 1.8rem 3.5rem;
  color: #222;
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: capitalize;
  text-decoration: none;
}

.projects {
  background-color: #d7a86e;
  padding: 2rem 0;
}
.projects h2 {
  font-size: 4.5rem;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700;
  padding: 10rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .projects h2 {
    padding: 10rem;
    font-size: 5rem;
  }
}
.projects p {
  background-color: rgba(192, 107, 37, 0.7215686275);
  font-weight: 700;
  padding: 2rem;
  text-align: center;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 2.4rem;
  text-transform: capitalize;
  color: #000;
}
.projects a {
  text-decoration: none;
  line-height: 0.7;
  transition: transform 0.3s ease-out;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.projects a:hover {
  transform: scale(1.2);
}
.projects img {
  width: 400px;
  height: 400px;
  object-fit: cover;
}
.projects .btn-show-all {
  display: block;
  margin: 8rem auto;
  padding: 2rem;
  width: fit-content;
  background-color: rgba(192, 107, 37, 0.7215686275);
  font-weight: 700;
  text-align: center;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 2.4rem;
  text-transform: capitalize;
  color: #000;
}

.projects-grid {
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 9rem;
  width: fit-content;
}
@media (min-width: 1024px) {
  .projects-grid {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1440px) {
  .projects-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.contact {
  background-color: #e3c19c;
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .contact {
    padding: 10rem;
  }
}
.contact h2 {
  font-size: 3.5rem;
  font-family: Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}
@media (min-width: 768px) {
  .contact h2 {
    font-size: 4rem;
  }
}

.contact-links {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  padding: 5rem 0 2rem 0;
}
@media (min-width: 768px) {
  .contact-links {
    gap: 8rem;
  }
}
.contact-links a {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-decoration: none;
  font-weight: 700;
  color: rgba(157, 71, 1, 0.7215686275);
  font-size: 4rem;
  transition: transform 300ms ease-out;
  opacity: 0.8;
  text-align: center;
}
.contact-links a:hover {
  transform: translateY(1rem);
  opacity: 1;
}

.footer {
  background-color: #ebd8c3;
}
.footer p {
  display: block;
  width: fit-content;
  margin: 0 auto;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 2rem;
  color: #000;
  padding: 3rem;
  opacity: 0.6;
  text-align: center;
}
.footer p:hover {
  opacity: 1;
}